/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.content {
  display: grid;
  grid-auto-columns: minmax(0, 54rem);
  grid-gap: 1.6rem;
  grid-template-columns: minmax(0, 35rem);
  align-content: center;
  justify-content: center;
  height: 100vh;
  padding: 6.4vmin;
  background: center/100% 100% url(bg-mobile.svg);
}
@media (min-width: 48rem) {
  .content {
    grid-auto-flow: column;
    grid-gap: 3rem;
  }
}
@media (min-width: 64rem) {
  .content {
    background: bottom/100% 52.5% no-repeat url(bg-desktop.svg), #0c122c;
  }
  .content > * {
    margin-bottom: 2.1rem;
  }
}

.control {
  padding: 4rem;
  background: #1d2c67;
  border-radius: 1rem;
  border-top-right-radius: 10rem;
}

.toolbar {
  display: grid;
  grid-auto-flow: column;
  grid-gap: 1.6rem;
  justify-content: start;
  margin-top: 3.2rem;
}
.toolbar > li {
  display: flex;
}

.iconBtn {
  width: 4.8rem;
  height: 4.8rem;
  padding: 0;
  padding: 0;
  background-color: #0c122c;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
.iconBtn:hover {
  filter: brightness(1.3);
}
.iconBtn:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem #848794;
}
.iconBtn:active {
  transform: translateY(0.2rem);
}

.newFile {
  background-image: url(icon-document.svg);
}

.newDir {
  background-image: url(icon-folder.svg);
}

.upload {
  background-image: url(icon-upload.svg);
}

.status {
  display: grid;
  grid-template: "stats" auto "badge" 1fr "badge" 1fr/1fr;
}
@media (min-width: 48rem) {
  .status {
    grid-template: "badge" 1fr "badge" 1fr "stats" auto/1fr;
  }
}

.bg {
  grid-area: 1/1/span 2;
  background: #1d2c67;
  border-radius: 1rem;
}
@media (min-width: 48rem) {
  .bg {
    grid-row-start: 2;
  }
}

.stats {
  display: block;
  grid-area: stats;
  margin-top: -0.7rem;
  padding: 3.2rem 3.2rem 1.6rem;
  text-align: center;
}
@media (min-width: 48rem) {
  .stats {
    padding: 0 4rem 4rem;
    text-align: left;
  }
}

.label {
  color: white;
  font: 1.4rem Raleway, sans-serif;
}
.label > span {
  font-weight: 700;
}

.meter {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;
  margin-top: 1.6rem;
}

.bar {
  --value: 0.815;
  width: 100%;
  height: 2rem;
  padding: 0.3rem;
  background: #0c122c;
  border-radius: 1rem;
}
.bar > div {
  position: relative;
  width: calc(1.4rem + (100% - 1.4rem) * var(--value));
  height: 100%;
  background: linear-gradient(to right, #ffa399, #ff4d97);
  border-radius: 0.7rem;
}
.bar > div::after {
  position: absolute;
  top: 0.2rem;
  right: 0.2rem;
  width: 1rem;
  height: 1rem;
  background: white;
  border-radius: 50%;
  content: "";
}

.limit {
  margin-top: 0.8rem;
  color: white;
  font: 700 1.2rem Raleway, sans-serif;
}

.badge {
  grid-area: badge;
  justify-self: center;
}
.badge > div {
  display: flex;
  align-items: center;
  padding: 1.2rem 2.4rem;
  color: #848794;
  font: 700 1.3rem Raleway, sans-serif;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  background: white;
  border-radius: 1rem;
}
.badge > div > span {
  margin-right: 0.8rem;
  color: #0c122c;
  font-size: 4rem;
  line-height: 120%;
}
@media (min-width: 48rem) {
  .badge > div {
    border-bottom-right-radius: 0;
  }
}
@media (min-width: 48rem) {
  .badge {
    justify-self: end;
    margin-right: 4rem;
  }
  .badge::after {
    display: block;
    width: 0;
    margin-top: -0.1rem;
    margin-left: auto;
    border-top: 2.3rem solid white;
    border-left: 2.3rem solid transparent;
    content: "";
  }
}

.hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}